import { GSPBusinessEntity } from '../GSPBusinessEntity';
import { BizAction } from '../Operation/BizAction';
import { GSPBizEntityObject } from '../GSPBizEntityObject';

import { Determination } from '../Operation/Determination';
import { Validation } from '../Operation/Validation';
import { BizOperation } from '../Operation/BizOperation';
import { BizMgrAction } from '../Operation/BizMgrAction';

export class BeOperationUtil {
  getAllMgrActions(be: GSPBusinessEntity): BizMgrAction[] {
    return be.BizMgrActions;
  }

  /**
   * bizActions
   */
  getAllBizEntityActions(be: GSPBusinessEntity): BizAction[] {
    return this.getChidrenObjectsActions(be.MainObject);
  }

  getChidrenObjectsActions(bizObject: GSPBizEntityObject): BizAction[] {
    const result = new Array<BizAction>();
    if (!(bizObject)) {
      return result;
    }
    if (bizObject.BizActions && bizObject.BizActions.length > 0) {
      result.push(...bizObject.BizActions);
    }
    if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
      for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
        result.push(...this.getChidrenObjectsActions(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
      }
    }
    return result;
  }

  /**
   * determination
   */
  getAllDeterminations(be: GSPBusinessEntity): Determination[] {
    return this.getChidrenObjectsDeterminations(be.MainObject);
  }

  getChidrenObjectsDeterminations(bizObject: GSPBizEntityObject): Determination[] {
    const result = new Array<Determination>();
    if (!(bizObject)) {
      return result;
    }
    if (bizObject.Determinations && bizObject.Determinations.length > 0) {
      result.push(...bizObject.Determinations);
    }
    if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
      for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
        result.push(...this.getChidrenObjectsDeterminations(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
      }
    }
    return result;
  }

  /**
   * validation
   */
  getAllValidations(be: GSPBusinessEntity): Validation[] {
    return this.getChidrenObjectsValidations(be.MainObject);
  }

  getChidrenObjectsValidations(bizObject: GSPBizEntityObject): Validation[] {
    const result = new Array<Validation>();
    if (!(bizObject)) {
      return result;
    }
    if (bizObject.Validations && bizObject.Validations.length > 0) {
      result.push(...bizObject.Validations);
    }
    if (bizObject.ContainChildObjects && bizObject.ContainChildObjects.length > 0) {
      for (let i = 0; i < bizObject.ContainChildObjects.length; i++) {
        result.push(...this.getChidrenObjectsValidations(bizObject.ContainChildObjects[i] as GSPBizEntityObject));
      }
    }
    return result;
  }

  /**
   * è·åæææä½
   */
  getAllOperations(be: GSPBusinessEntity): BizOperation[] {
    const result = new Array<BizOperation>();
    result.push(...this.getAllBizEntityActions(be));
    result.push(...this.getAllMgrActions(be));
    result.push(...this.getAllDeterminations(be));
    result.push(...this.getAllValidations(be));
    return result;
  }

  /**
   * validationè§¦åå­æ®µä¸­æ¯å¦åå«æå­æ®µ
   * @param eleId å­æ®µID
   */
  countainValidationRequestElement(be: GSPBusinessEntity, eleId: string): string {
    const validations = this.getAllValidations(be);
    if (!!(validations) && validations.length > 0) {
      for (let i = 0; i < validations.length; i++) {
        const b = this.validationCountainRequestElement(validations[i], eleId);
        if (b) {
          const result = validations[i].Name;
          return result;
        }
      }
    }
    return null;
  }

  /**
   * determinationè§¦åå­æ®µä¸­æ¯å¦åå«æå­æ®µ
   * @param eleId å­æ®µID
   */
  countainDeterminationRequestElement(be: GSPBusinessEntity, eleId: string): string {
    const dtms = this.getAllDeterminations(be);
    if (!!(dtms) && dtms.length > 0) {
      for (let i = 0; i < dtms.length; i++) {
        const b = this.determinationCountainRequestElement(dtms[i], eleId);
        if (b) {
          const result = dtms[i].Name;
          return result;
        }
      }
    }
    return null;
  }

  private countainEle(id: string, requestEles: string[]): boolean {
    if (!!(id) && !!(requestEles) && requestEles.length > 0) {
      return requestEles.indexOf(id) >= 0;
    }
    return false;
  }

  /**
   * è§¦åå­æ®µä¸­æ¯å¦åå«æå­æ®µ
   */
  determinationCountainRequestElement(dtm: Determination, id: string): boolean {
    if (this.countainEle(id, dtm.RequestElements)) {
      return true;
    }
    if (!!(dtm.RequestChildElements) && dtm.RequestChildElements.length > 0) {
      return this.countainEle(id, dtm.RequestChildElements[0].RequestChildElementValue);
    }
    return false;
  }

  /**
   * è§¦åå­æ®µä¸­æ¯å¦åå«æå­æ®µ
   */
  validationCountainRequestElement(validation: Validation, id: string): boolean {
    if (this.countainEle(id, validation.RequestElements)) {
      return true;
    }
    if (!!(validation.RequestChildElements) && validation.RequestChildElements.length > 0) {
      return this.countainEle(id, validation.RequestChildElements[0].RequestChildElementValue);
    }
    return false;
  }
}
